const BASE_URL = 'https://roughs.ru/api/talker';

export default class QuestionApi {
  constructor() {
    this.searchQuery = '';
  }
  async fetchAnswer() {
    try {
      const response =
        await fetch(`${BASE_URL}?text=${this.searchQuery}&source_from=http://localhost:1234/
`);
      const data = await response.json();
      const dataList = await data;
      console.log(dataList);
      return dataList;
    } catch {}
  }

  get query() {
    return this.searchQuery;
  }
  set query(newQuery) {
    this.searchQuery = newQuery;
  }
}
